<template>
    <div>
        <span v-for="(part, idx) in stringArray" :style="style(idx)" :key="idx"
        >{{ part }}</span>
    </div>
</template>

<script>
export default {
    name: "GrepLine",
    props: {
        str: { default: '' },
        searcher: { default: '' },
        mainColor: { default: 'black' },
        highlightColor: { default: 'red' },
    },
    data() {
        return {
            normalStyle: {
                color: this.mainColor,
            },
            selectionStyle: {
                color: this.highlightColor,
                'font-weight': 'bold',
            },
        }

    },
    computed: {
        stringArray() {
            if (!this.searcher) {
                return [this.str]
            }
            const splitter = `(${this.searcher})`;
            const reg = new RegExp(splitter, 'gi');
            return this.str.split(reg);
        }
    },
    methods: {
        style(idx) {
            return idx % 2 ? this.selectionStyle : this.normalStyle;
        }
    }
}
</script>

