<template>
    <div v-if="line" class="line-card" :id="line.hash">
        <div class="controls-wrapper">
            <div class="controls">
                <div class="control-sign" @click="flashHash">#</div>
                <div v-if="isSearch" class="control-sign" @click="onClick"></div>
            </div>
        </div>
        <div class="lines">
            <GrepLine v-for="({ str, searcher, mainColor, key, isTitleLine }) in dataArray"
                      :str="str"
                      :searcher="searcher"
                      :main-color="mainColor"
                      :key="key"
                      class="mb-1"
            ></GrepLine>
            <div><br></div>
        </div>

    </div>
</template>

<script>
import GrepLine from '@/components/corpusWithTranslation/GrepLine';
import { DisplayModes, LineNames } from '@/components/corpusWithTranslation/constants';
import { markOnHash } from '@/services/markOnHash';
import { emptify } from '@/services/emptifyObject';


export default {
    name: "LineCard",
    props: {
        hasHash: { default: false },
        line: { required: true },
        searchers: { default: () => ({}) },
        mainColors: {
            default: () => ({
                titleLine: 'purple',
                textus: 'black',
                versio: 'darkblue',
            }),
        }
    },
    data() {
        return {
            LineNames: LineNames,
        }
    },
    components: {
        GrepLine,
    },
    computed: {
        dataArray() {
            return [this.LineNames.titleLine, this.LineNames.textus, this.LineNames.versio].map((key, i) => ({
                str: this.line[key],
                searcher: this.searchers[key],
                mainColor: this.mainColors[key],
                key,
                get isTitleLine() {
                    return key === LineNames.titleLine;
                }
            }))
        },
        isSearch() {
            return !_.isEmpty(emptify(this.searchers));
        },
    },
    methods: {
        markOnHash,
        onClick() {
            if (!this.isSearch) {
                return;
            }
            const { volumeName, textNumber } = this.line;
            this.$router.push({
                query: {
                    mode: DisplayModes.browse,
                    volume: volumeName,
                    text: textNumber,
                },
            });
            this.flashHash();
        },
        flashHash() {
            this.markOnHash('#' + this.line.hash);
        }
    }
}
</script>

