@import "../../styles/variables.scss";

.line-card {
    display: flex;
}
.controls-wrapper {
    width: $small-box-width;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.controls {
    display: flex;
    flex-direction: column;
    align-items: center;
    user-select: none;
    .control-sign {
        color: lightgrey;
        cursor: pointer;
        &:hover {
            color: seagreen;
            font-weight: bold;
        }
        &:first-child {
            margin-bottom: 10px;
        }
    }
}
.lines {
    flex: 1;
}
</style>
