<template>
    <div class="d-flex align-center flex-column">
        <div class="searchers d-flex mt-5">
            <KeyboardIcon
                @click="activateKeyboard"
            ></KeyboardIcon>
            <v-text-field
                :value="searchersCurrent.textus"
                :label="textusLabel"
                outlined dense clearable
                @input="onInput($event, 'textus')"
                @keypress.enter.native.prevent="doSearch"
            >
            </v-text-field>
            <v-text-field
                :value="searchersCurrent.versio"
                :label="versioLabel"
                outlined dense clearable
                @input="onInput($event, 'versio')"
                @keypress.enter.native.prevent="doSearch"
            ></v-text-field>
            <v-btn :disabled="isSearchDisabled"
                   depressed tile
                   @click="doSearch"
            >Search</v-btn>
        </div>

        <v-dialog
            v-model="isKeyboardOpen"
            hide-overlay fullscreen
        >
            <Keyboard
                v-if="isKeyboardOpen"
                :buttons="buttonsArray"
                :value="searchersCurrent.textus"
                :input-name="textusLabel"
                @close="isKeyboardOpen = false"
                @submit="onKeyboardSubmit"
            ></Keyboard>
        </v-dialog>
    </div>
</template>

<script>
import LineCard from '@/components/corpusWithTranslation/LineCard';
import { DisplayModes, LineNames } from '@/components/corpusWithTranslation/constants';
import Keyboard from '@/components/common/Keyboard';
import KeyboardIcon from '@/components/common/KeyboardIcon';

export default {
    name: "Search",
    props: {
        corpusArrProp: { default: () => ([])},
        textusLabel: { default: 'Search corpus' },
        versioLabel: { default: 'Search translation'},
        corpusName: { type: String }
    },
    components: {
        LineCard,
        Keyboard,
        KeyboardIcon
    },
    data() {
        return {
            searchersCurrent: {
                textus: '',
                versio: '',
            },
            searchers: {
                textus: '',
                versio: '',
            },
            corpusArr: [],
            LineNames: LineNames,
            isKeyboardOpen: false,
        }
    },
    watch: {
        corpusArrProp: {
            deep: true,
            immediate: true,
            handler(value) {
                this.corpusArr = value;
            }
        }
    },
    computed: {
        isSearchDisabled() {
            return !this.searchersCurrent.textus && !this.searchersCurrent.versio;
        },
        buttonsArray() {
            if (!this.corpusName) {
                return [];
            }
            return this.$store.getters.buttonsArrayByName(this.corpusName);
        }
    },
    methods: {
        onInput(value, searcher) {
            this.searchersCurrent[searcher] = value;
            if (!value) {
                this.doSearch();
            }
        },
        onKeyboardSubmit(value) {
            this.searchersCurrent.textus = value;
        },
        doSearch() {
            this.searchers = _.cloneDeep(this.searchersCurrent);
            this.$emit('searchers', _.cloneDeep(this.searchers));
            this.setQueryToUrl();
        },
        setQueryToUrl() {
            const textusPart = this.searchers.textus ? { textus: this.searchers.textus } : {};
            const versioPart = this.searchers.versio ? { versio: this.searchers.versio } : {};
            const query = { mode: DisplayModes.search, ...textusPart, ...versioPart };
            if (_.isEqual(query, this.$route.query)) {
                return;
            }
            this.$router.push({ query, hash: '' });
        },
        activateKeyboard() {
            this.isKeyboardOpen = true;
        },
        copyToClipboard() {
            this.$emit('copy-to-clipboard');
        }
    },
    mounted() {
        if (this.$route.query.textus) {
            this.searchersCurrent.textus = this.$route.query.textus;
        }
        if (this.$route.query.versio) {
            this.searchersCurrent.versio = this.$route.query.versio;
        }
        if (this.searchersCurrent.textus || this.searchersCurrent.versio) {
            this.doSearch();
        }
    }
}
</script>

<style lang="scss" scoped>
.searchers {
    & > *:not(:last-child) {
        margin-right: 20px;
    }
}

.results {
    width: 100%;
}
</style>
