<template>
    <div>
        <div v-if="!disable">
            <slot :items="chunks[0]"></slot>
            <v-lazy
                v-model="scrollModel[i]"
                v-for="(chunk, i) of chunks.slice(1)"
                :key="'chunk' + i"
                :options="{ threshold }"
                :min-height="minHeight"
            >
                <div>
                    <slot :items="chunk"></slot>
                </div>
            </v-lazy>
        </div>
        <div v-else>
            <div v-for="(chunk, i) of chunks"
                 :key="'chunk' + i">
                <slot :items="chunk"></slot>
            </div>
        </div>
    </div>
</template>

<script>
import _ from 'lodash';
export default {
    name: 'InfiniteScroll',
    props: {
        items: { default: () => []},
        chunkSize: { default: 20 },
        threshold: { default: 0.3 },
        minHeight: { default: 200 },
        disable: { type: Boolean, default: false },
    },
    data() {
        return {
            scrollModel: [],
        }
    },
    computed: {
        chunks() {
            return _.chunk(this.items, this.chunkSize);
        }
    }
}
</script>
