<template>
    <div>
        <div class="selects d-flex mt-5 mb-8">
            <v-select
                :value="chosen.volume"
                :items="corpusArr"
                :label="volumeLabel"
                item-text="volumeName"
                item-value="volumeName"
                outlined dense
                @change="onChangeChosen($event, 'volume')"
            ></v-select>
            <v-select
                :value="chosen.text"
                :items="chosenVolumeTexts"
                :item-text="displayTextName"
                :label="textLabel"
                :no-data-text="`A ${volumeLabel} should be chosen`"
                item-value="textNumber"
                outlined dense
                @change="onChangeChosen($event, 'text')"
            ></v-select>
        </div>
    </div>
</template>

<script>

import LineCard from '@/components/corpusWithTranslation/LineCard';
import { DisplayModes } from '@/components/corpusWithTranslation/constants';

export default {
    name: "Browse",
    props: {
        corpusArrProp: { default: () => ([])},
        volumeLabel: { default: 'Volume' },
        textLabel: { default: 'Text'},
        volumeName: '',
        textNumber: '',
    },
    components: {
        LineCard,
    },
    data() {
        return {
            chosen: {
                volume: '',
                text: '',
            },
            corpusArr: [],
        };
    },
    watch: {
        corpusArrProp: {
            deep: true,
            immediate: true,
            handler(value) {
                this.corpusArr = value;
            },
        },
        '$route': {
            deep: true,
            immediate: true,
            handler(route) {
                const query = route.query;
                this.chosen.volume = query.volume;
                if (query.volume) {
                    this.emitEmpty();
                }
                this.chosen.text = query.text;
                if (query.text) {
                    this.emitChosen();
                }
            }
        }
    },
    computed: {
        chosenVolume() {
            if (!this.chosen.volume) {
                return;
            }
            return this.corpusArr.find(({ volumeName }) => volumeName === this.chosen.volume);
        },
        chosenVolumeTexts() {
            if (!this.chosenVolume) {
                return [];
            }
            return this.chosenVolume.texts;
        },
    },
    methods: {
        displayTextName(text) {
            return text.textName.replace(/\.txt/, '');
        },
        setQuery() {
            const volumePart = this.chosen.volume ? { volume: this.chosen.volume } : {};
            const textPart = this.chosen.text ? { text: this.chosen.text } : {};
            const query = { mode: DisplayModes.browse, ...volumePart, ...textPart };
            this.$router.push({ query });
        },
        onChangeChosen(value, entity) {
            this.chosen[entity] = value;
            if (entity === 'volume') {
                this.chosen.text = '';
                this.emitEmpty();
            } else {
                this.emitChosen();
            }
            this.setQuery();
        },
        emitChosen() {
            this.$emit('chosen', this.chosen);
        },
        emitEmpty() {
            this.$emit('chosen', {})
        }
    }
}
</script>

<style lang="scss" scoped>
.selects {
    & > *:not(:last-child) {
        margin-right: 20px;
        max-width: 30%;
    }
}
</style>
