<template>
    <div class="results">
        <div v-if="hasStats" class="results-stats">
            <div :style="resultsStatsStyle">Found {{ results.length }} contexts</div>
            <div><br></div>
        </div>
        <div v-if="doShowWarning" class="warning-wrapper" :style="warningStyle">
            <div class="many-lines-warning">
                <p>{{ warningText }}</p>
                <div class="buttons">
                    <v-btn depressed color="warning" @click="showMany">Render on screen</v-btn>
                    <v-btn depressed color="success" @click="cancelSearch">Cancel</v-btn>
                </div>
            </div>
        </div>
        <div v-else>
            <InfiniteScroll :items="results" v-if="results" :disable="disableInfiniteScroll || results.length < 500">
                <template v-slot:default="{ items }">
                    <LineCard v-if="line"
                              v-for="line in items"
                              :line="line"
                              :searchers="searchers"
                              :key="line.key"
                    ></LineCard>
                </template>
            </InfiniteScroll>
        </div>

    </div>

</template>

<script>
import LineCard from '@/components/corpusWithTranslation/LineCard';
import { emptify } from '@/services/emptifyObject';
import { makeFlashes } from '@/services/makeFlashes';
import InfiniteScroll from '@/components/common/InfiniteScroll';

export default {
    name: "Results",
    components: {
        LineCard,
        InfiniteScroll,
    },
    data() {
        return {
            manyLinesLimit: 1000,
            hideManyLines: true,
            warningStyle: '',
        }
    },
    props: ['results', 'searchers', 'disableInfiniteScroll'],
    computed: {
        hasStats() {
            return emptify(this.searchers);
        },
        resultsStatsStyle() {
            return 'color: green; font-weight: bold';
        },
        doShowWarning() {
            return false;
            // return (this.results.length > this.manyLinesLimit) && this.hideManyLines;
        },
        warningText() {
            return `The number of found contexts (${this.results.length}) is too large,
                    so rendering them may crash or freeze your browser.
                    Do you still want to show the found contexts
                    or rather to cancel the search and make
                    the search conditions more specific?`
        },
    },
    watch: {
        doShowWarning: {
            immediate: true,
            handler(value) {
                if (value) {
                    const num = 2;
                    const interval = 500;
                    const warningStyles = ['none', 'flex'].map((display) => `display: ${display}`);
                    const makeFn = (style) => () => this.warningStyle = style;
                    const [fnIn, fnOut] = warningStyles.map(makeFn);
                    makeFlashes({ num, interval, fnIn, fnOut });
                }
            }
        }
    },
    methods: {
        showMany() {
            this.hideManyLines = false;
        },
        cancelSearch() {
            this.$emit('cancel-search');
        }
    }
}
</script>

<style scoped lang="scss">
@import "../../styles/variables.scss";

.results-stats {
    padding-left: $small-box-width;
    margin-top: 25px;
}

.warning-wrapper {
    display: flex;
    justify-content: center;
}
.many-lines-warning {
    width: 600px;
    color: red;
    font-weight: bold;
    padding: 20px;
    border: 3px solid red;
    .buttons {
        display: flex;
        justify-content: space-around;
    }
}

</style>
