import { makeFlashes } from '@/services/makeFlashes';

export function markOnHash(hash) {
    setTimeout(() => {
        location.hash = hash;
        setTimeout(() =>  window.scrollBy(0, -200), 0);
        const hashEl = document.querySelector(hash);
        if (!hashEl) {
            return;
        }
        const interval = 500;
        const num = 3;
        const fnIn = () => hashEl.style.background = 'lightgreen';
        const fnOut = () => hashEl.style.background = '';
        makeFlashes({ num, interval, fnIn, fnOut });
    }, 300);
}
