import _ from 'lodash';

export function makeFlashes({ num, interval, fnIn, fnOut, fnEnd = () => null }) {
    if (!(
        _.isNumber(num)
        && num > 1
        && _.isNumber(interval)
        && interval > 100
        && _.isFunction(fnIn)
        && _.isFunction(fnOut)
        && _.isFunction(fnEnd)
    )) {
        return;
    }
    let count = 0;
    const flashesInterval = setInterval(() => {
        if (++count > num) {
            clearInterval(flashesInterval);
            fnEnd();
        }
        fnIn();
        setTimeout(fnOut, interval / 2);
    }, interval);
}
