<template>
    <div v-if="corpusName" :class="{ 'no-hashes': hasWindowSelection }">
        <div v-if="displayMode === DisplayModes.search" class="searcher-wrapper">
            <v-btn ref="search_btn"
                   large
                   class="mr-10 mb-2"
                   depressed tile
                   min-width="220px"
                   :loading="copyToClipboardBtn.loading"
                   @click="copyToClipboardProxy"
            >{{ copyToClipboardBtn.text }}</v-btn>
            <Search
                :corpus-arr-prop="corpusArr"
                :corpus-name="corpusName"
                textus-label="Aramaic text"
                versio-label="German text"
                @searchers="onSearchers"
                @copy-to-clipboard="copyToClipboardProxy"
            ></Search>
        </div>
        <Browse v-else-if="displayMode === DisplayModes.browse"
                :corpus-arr-prop="corpusArr"
                @chosen="onChosenText"
        ></Browse>
        <Results v-if="results.length"
                 :results="results"
                 :searchers="searchers"
                 :disable-infinite-scroll="disableInfiniteScroll"
                 @cancel-search="onCancelSearch"
        ></Results>
        <v-snackbar
            v-model="snackbar.isVisible"
            :color="snackbar.color"
            top left outlined
        >{{ snackbar.text }}</v-snackbar>
    </div>
</template>

<script>
import Search from '@/components/corpusWithTranslation/Search';
import Browse from '@/components/corpusWithTranslation/Browse';
import { ButtonOption } from '@/store/commonStore';
import { markOnHash } from '@/services/markOnHash';
import Results from '@/components/corpusWithTranslation/Results';
import { Line } from '@/services/corpusWithTranslationClasses';
import { DisplayModes } from '@/components/corpusWithTranslation/constants';
import copyToClipboardManyMixin from '@/mixins/copyToClipboardManyMixin';
import { sleep } from '@/services/sleep';

const empty = {
    chosen: {
        volume: null,
        text: null,
    },
    searchers: {
        textus: '',
        versio: '',
    }
}


export default {
    name: "BrowseAndSearch",
    components: {
        Search,
        Browse,
        Results
    },
    mixins: [copyToClipboardManyMixin],
    props: {
        corpusName: { required: true },
    },
    data() {
        return {
            DisplayModes: DisplayModes,
            displayMode: DisplayModes.search,
            volumeName: '',
            textNumber: '',
            hasWindowSelection: false,
            searchers: _.cloneDeep(empty.searchers),
            chosen: _.cloneDeep(empty.chosen),
            selectionInterval: null,
            searchResultsElement: null,
            snackbar: {
                isVisible: false,
                text: '',
                color: 'success'
            },
            disableInfiniteScroll: false,
            copyToClipboardBtn: {
                text: 'Copy to Clipboard',
                loading: false,
            },
        }
    },
    computed: {
        otherMode() {
            return Object.values(DisplayModes).filter((mode) => mode !== this.displayMode)[0];
        },
        buttonOptions() {
            return [this.displayMode, this.otherMode]
                .map((mode) =>  new ButtonOption({
                    text: mode,
                    isChosen: mode === this.displayMode,
                    byClick: false,
                }))
                .sort((option) => 0.5 - Number(option.isChosen));
        },
        displayModeFromSwitcher() {
            const chosenOption = this.$store.getters.chosenOption;
            if (chosenOption) {
                return chosenOption.text;
            }
            return '';
        },
        corpusArr() {
            return this.$store.getters.corpusArrayByName(this.corpusName);
        },
        chosenTextLines() {
            if (!this.chosen.text) {
                return [];
            }
            const chosenVolume = this.corpusArr.find(({ volumeName }) => volumeName === this.chosen.volume);
            return chosenVolume.texts.find(({ textNumber }) => textNumber === this.chosen.text).contents;
        },
        isNoSearchers() {
            return !this.searchers.textus && !this.searchers.versio;
        },
        searchResults() {
            if (this.isNoSearchers) {
                return [];
            }
            const result = [];
            for (const volume of this.corpusArr) {
                const { volumeName } = volume;
                for (const text of volume.texts) {
                    const { textNumber, textName } = text;
                    for (const line of text.contents) {
                        const regTextus = new RegExp(this.searchers.textus || '', 'gi')
                        const regVersio = new RegExp(this.searchers.versio || '', 'gi')
                        if (
                            (!this.searchers.textus || line.textus.match(regTextus))
                            && (!this.searchers.versio || line.versio.match(regVersio))
                        ) {
                            const resultLine = new Line({ volumeName, textNumber, textName, ...line });
                            result.push(resultLine);
                        }
                    }
                }
            }
            return result;
        },
        results() {
            return this.displayMode === this.DisplayModes.browse ? this.chosenTextLines : this.searchResults;
        },
    },
    watch: {
        displayModeFromSwitcher(value) {
            if (!value) {
                return;
            }
            this.displayMode = value;
            this.$router.push({ query: { mode: value } });
            this.searchers = {};
            this.chosen = _.cloneDeep(empty.chosen);
        },
        displayMode(value, prev) {
            if (value !== prev) {
                this.setSwitcherButtons();
            }
        },
        '$route': {
            deep: true,
            immediate: true,
            handler(route) {
                if (route.query.mode) {
                    this.displayMode = route.query.mode;
                }
                if (this.displayMode === this.DisplayModes.search) {
                    this.chosen = _.cloneDeep(empty.chosen);
                } else if (this.displayMode === this.DisplayModes.browse) {
                    this.searchers = _.cloneDeep(empty.searchers);
                }
            },
        },
    },
    methods: {
        markOnHash,
        setSwitcherButtons() {
            this.$store.commit('setButtonSwitcherOptions', this.buttonOptions);
        },
        onSearchers(value) {
            this.searchers = value;
        },
        onChosenText(chosenObj) {
            this.searchers = _.cloneDeep(empty.searchers);
            this.chosen.volume = chosenObj.volume;
            this.chosen.text = chosenObj.text;
        },
        async copyToClipboardProxy() {
            this.copyToClipboardBtn.loading = true;
            await sleep(100);
            this.disableInfiniteScroll = true;
            await this.$nextTick();
            const selectorStats = '.results .results-stats';
            const statsElem = document.querySelector(selectorStats);
            const selectorLineCards = '.results .lines';
            const lineCards = Array.from(document.querySelectorAll(selectorLineCards));
            const success = await this.copyToClipboardMany([statsElem, ...lineCards]);
            if (success) {
                this.snackbar.text = 'Search results successfully copied to clipboard!';
                this.snackbar.color = 'success';
            } else {
                this.snackbar.text = 'Something went wrong  nothing copied!';
                this.snackbar.color = 'error';
            }
            this.snackbar.isVisible = true;
            setTimeout(() => this.disableInfiniteScroll = false, 1000);
            this.copyToClipboardBtn.loading = false;
        },
        onCancelSearch() {
            this.searchers = _.cloneDeep(empty.searchers);
        }
    },
    created() {
        this.$store.commit('makeTextsByCorpusName', this.corpusName);
        this.setSwitcherButtons();
        this.selectionInterval = setInterval(() => {
            this.hasWindowSelection = window.getSelection().toString();
        }, 100);
        if (!this.$route.query.mode) {
            this.$router.replace({ query: { ...this.$route.query, mode: this.displayMode } });
        }
        const hash = this.$route.hash;
        if (hash) {
            this.markOnHash(hash);
        }
    },
    beforeDestroy() {
        this.$store.commit('deleteButtonSwitcherOptions');
        clearInterval(this.selectionInterval);
    }
}
</script>

<style scoped lang="scss">
@import '../../styles/variables';
.searcher-wrapper {
    display: flex;
    align-items: center;
}


.no-hashes {
    ::v-deep .controls-wrapper {
        display: none;
    }
    ::v-deep .results .line-card {
        padding-left: $small-box-width;
    }
}

::v-deep .v-snack__wrapper {
    margin-top: 20px;
    margin-left: 70px;

    .v-snack__content {
        font-size: 20px;
        padding: 20px 25px;
    }
}
</style>
